.background-image {
  background-color: #ffffff40;
  width: 100%;
  height: 100vh;
}
.header-bar {
  width: 100%;
  height: 70px;
  top: -25px;
  position: relative;
}
.container {
  padding: 10px;
  background-color: black;
  border-radius: 5px;
}
.card-box {
  /* background-color: #F0C725; */
  /* border-radius: 5px; */
  /* padding: 10px 20px; */
  color: white;
  margin: 5px 10px;
  text-align: center;
}
.display-box {
  background-color: rgb(233, 216, 216);
  padding: 15px;
  border: 1px solid #FE6D02;
  border-radius: 10px;
  color: black;
}
.uncheck-box {
  background-color: white;
  padding: 15px;
  color: black;
  text-align: center;
}
.display-button {
  background-color: #0a1825 !important;
  color: white !important;
  padding: 10px 20px !important;
  border-radius: 5px !important;
}
.pool-button {
  background-color: #e71212 !important;
  border-top: 2px solid #eddd06 !important;
  color: white !important;
  padding: 20px 50px !important;
  border-radius: 5px !important;
  margin: 10px !important;
}
.disable-button {
  color: white !important;
  background-color: #b6a7da !important;
  padding: 10px 20px !important;
  border-radius: 5px !important;
}
.loading {
  display: flex;
  background-color: rgba(0,0,0,0.5);
  position: fixed;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 100vh;
  justify-content: center;
  align-items: center;
}
.timer {
  background-color: rgb(24, 4, 124);
  color: white !important;
  width: 500px;
  border-radius: 10px;
  border: 1px solid white;
  /* box-shadow: 0px 0px 5px lightgrey; */
  padding: 25px 35px;
  color: black;
  margin: 0px 15px;
}
.icon-style {
  color: black;
  font-size: 40px !important;
}
.icon-style:hover {
  color: #ec8dc7;
  cursor: pointer;
}